.jobOpenings__banner {
  margin-bottom: 7px;
}

.jobOpenings__infoTitle{
  font-size: 1.125em;
  font-weight: 600;
  line-height: 1.4;
  margin: 0;
}

.jobOpenings__header {
  /* margin-bottom: 8px; */
  margin-bottom: 18px;
}

.comp_logo{
  /* width: 35%; */
  margin-right: 8px;
}

.jobOpenings__image {
  background: url("https://images.jumpstart.me/frontend/icons/company_logo_default.svg");

  background-size: contain;
  background-size: cover;
  border-radius: 50%;
  /* width: 30%; */
  margin-right: 5px;
  height: 3.625rem;
  width: 3.625rem;
  min-height: 3.625rem;
  min-width: 3.625rem;
  flex-shrink: 0;
  margin-bottom: 10px;
}

.application {
  color: #788699;
  transition: opacity 125ms cubic-bezier(0.25, 0.1, 0.25, 1);
  opacity: 0;
}

